.btn {
  cursor: pointer;
  border: none;
  border-radius: 74px;
  margin: 0 auto;
  padding: 12px 24px;
  font-family: inherit;
  font-weight: 500;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.btn__orange-gradient {
  color: var(--background);
  background: linear-gradient(145deg, #ffc226 0%, #f84119 100%);
}

.btn.btn__orange-gradient:hover, .btn.btn__orange-gradient:active {
  background-color: var(--white);
  color: var(--orange);
  background: none;
  border: 1px solid red;
}

.btn__black {
  background-color: var(--background);
  color: var(--white);
  border: 1px var(--orange) solid;
}

.btn.btn__black:hover, .btn.btn__black:active {
  background-color: var(--white);
  color: var(--orange);
  border: var(--background);
}

.btn__small {
  font-size: 12px;
  line-height: 1.33333;
}

@media (min-width: 768px) and (max-width: 1280px) {
