}
.hero_background {
  position: relative;
  width: 320px;
  height: 380px;

  background: linear-gradient(
      86.77deg,
      #111111 30.38%,
      rgba(17, 17, 17, 0) 65.61%
    ),
    url('../images/hero_mobile.png') no-repeat center center;
  background-size: cover, cover;
}
