@media (min-width: 768px) and (max-width: 1279px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }

  .rbt-bold {
    font-size: 52px;
  }

  .hero_background {
    position: relative;
    width: 768px;
    height: 432px;

    background: linear-gradient(76deg, #111 6.82%, rgba(17, 17, 17, 0) 100%),
      url('../images/hero_tablet.png') no-repeat left center;
    background-size: cover, cover;
  }

  .hero_text_1 {
    padding-top: 40px;
    width: 332px;
    height: 104px;
    left: 20px;
    font-size: 44px;
    line-height: normal;
    font-weight: bold;
  }

  .hero_text_2 {
    width: 357px;
    height: 64px;
    margin-top: 50px;

    line-height: normal;
    font-size: 14px;
    font-weight: 100;
  }
  .hero_button {
    top: 260px;
    left: 30px;
  }
  .buttons {
    position: absolute;
    display: flex;
    top: 270px;
    gap: 10px;
    justify-content: center;
  }
}
@media (width >=1280px) {
  .container {
    max-width: 1280px;
    margin: 0 auto;
  }
  .hero_background {
    position: relative;
    width: 1280px;
    height: 720px;

    background: linear-gradient(69deg, #111 19.23%, rgba(17, 17, 17, 0) 100%),
      url('../images/hero_desktop.png') no-repeat left center;
    background-size: cover, cover;
  }
  .hero_text_1 {
    padding-top: 62px;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 44px;
    color: var(--white);
  }
  .hero_text_2 {
    position: absolute;
    top: 160px;
    margin-top: 50px;
    width: 464px;
    height: 76px;
    font-size: 16px;
  }
  .buttons {
    position: absolute;
    display: flex;
    top: 345px;
    gap: 10px;
    justify-content: center;
  }
}

